@font-face {
  font-family: "nasa";
  src: url('./fonts/nasalization-rg.ttf');
}
html, body {
  font-family: 'nasa', sans-serif;
  background: url('https://images.squarespace-cdn.com/content/v1/57e82f41f5e23123e59c66ef/1558736594063-131GZH9N90C4UN67XMKC/ke17ZwdGBToddI8pDm48kOxq7JwFdX-CJiUhVAhKCBwUqsxRUqqbr1mOJYKfIPR7LoDQ9mXPOjoJoqy81S2I8N_N4V1vUb5AoIIIbLZhVYxCRW4BPu10St3TBAUQYVKcR49rFmqW3Qm3DWeVdIYyjrwDw5jJnpgiY5sKk4pfMR4BNv37NfmUu4UozZ-y8Hm4/mars-short-read-overlay1.jpg?format=1500w') no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100vh;
}
.App {
  text-align: center;
  height: auto;
  width: 100%;
  height: 100vh;
  display: grid;
  
}
.App-header {
  background-color: #eb4605;
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: 8px 8px 7px black;
}
.App-header img {
  width: 150px;
  height: auto;
}

.App-header h1 {
  font-size: 3em;
  font-weight: bold;
}
.content {
  width: 100%;
  height: auto;
  display: flex;
}
.Info-container {
  width: 75%;
  background-color: rgb(0, 0, 0, .9);
  border-radius: 10px;
  margin: 0 auto;
  
}
.Info-container h1 {
  color: #e53904;
  font-size: 2em;
  font-weight: 900;
}
.Info-container h3 {
  color: white;
  opacity: .83;
  font-size: 1.25em;
}
.Info-container p {
  color: white;
  text-align: center;
  padding: 1em;
}

footer {
  background-color: #282C34;
  color: white;
  font-weight: bold;
  font-size: .5em;
  height: 2em;
  padding: 5px;
  bottom: 0;
  left: 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  justify-content: center;
  vertical-align: center
  
}
Button {
  margin: 5px;
}
/* Mobile Screen Settings */
@media screen and (max-width: 992px) {
  .content {
    display: grid;
  }
  .Info-container {
    margin: 5% auto;
  }
  .App-header {
    width: 100%;
    height: auto;
    
  }
  .App-header img {
   display: none;
  }
  .App-header h1 {
    font-size: 2em;
  }
}